.list__container{display:flex;flex-direction:column;align-items:center;background-color:#e8e6e6}@media(min-width: 768px){.list__container{padding:60px 16%}}.list__list{display:flex;flex-direction:column;gap:40px;background-color:#e8e6e6;padding:30px 20px}@media(min-width: 768px){.list__list{display:grid;grid-template-columns:repeat(3, 1fr);padding:0}}.list .card{position:relative;display:flex;flex-direction:column;background-color:#dbdbdb;padding:34px;box-sizing:border-box}.list .card .card__img1,.list .card .card__img2,.list .card .card__img3{position:absolute;top:-25px;left:50%;transform:translateX(-50%);width:80px;height:80px;background-size:cover;background-position:center}.list .card__title{font-family:"Geometria Medium";font-size:20px;font-weight:400;line-height:1.4em;letter-spacing:0em;text-align:center;color:#4ca89d;margin-top:50px}.list .card__text{font-family:"Geometria Light";font-size:16px;font-weight:400;line-height:1.6em;text-align:center;color:#000;margin-top:12px}@media(min-width: 768px){.list .card__text{margin-top:20px}}.list .card__footer{display:flex;justify-content:center;margin-top:auto}.list .card__button{display:inline-block;padding:8px 32px;margin-top:24px;width:100%;text-align:center;background-color:#4ca89d;color:#fff;border-radius:8px;text-decoration:none;letter-spacing:0;border:1px solid #4ca89d}.list .card__button:hover{background-color:#fff;color:#4ca89d}.list p strong{font-family:"Geometria Medium"}